.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Add the CSS styles for your specific components below */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  background: #ffffff;
  border-radius: 10px;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #2c3e50;
  padding: 15px 20px;
  border-radius: 10px;
}

nav h2 {
  color: #ecf0f1;
  font-size: 24px;
}

nav a {
  color: #ecf0f1;
  text-decoration: none;
  padding: 10px;
  border-radius: 5px;
  transition: background 0.3s;
}

nav a:hover {
  background: rgba(236, 240, 241, 0.3);
}

h1 {
  color: #2980b9;
  text-align: center;
  font-size: 2.5em;
  margin-bottom: 20px;
}

h2 {
  color: #34495e;
  margin-bottom: 10px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 30px;
}

table th, table td {
  padding: 12px;
  text-align: left;
  border-bottom: 1px solid #bdc3c7;
}

table th {
  background-color: #2980b9;
  color: white;
}

table tr:hover {
  background-color: #ecf0f1;
}

.currency-converter {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px 0;
}

.currency-converter select, 
.currency-converter input {
  width: 150px;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid #bdc3c7;
  border-radius: 5px;
  transition: border-color 0.3s;
}

.currency-converter select:focus,
.currency-converter input:focus {
  border-color: #2980b9;
}

button {
  padding: 10px 20px;
  border: none;
  background-color: #2980b9;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.3s;
}

button:hover {
  background-color: #3498db;
}

footer {
  text-align: center;
  padding: 20px;
  margin-top: 20px;
  background-color: #2c3e50;
  color: #ecf0f1;
  border-radius: 10px;
}

@media (max-width: 768px) {
  nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .currency-converter {
    width: 100%;
  }

  table th, table td {
    font-size: 0.9em;
  }
}
