footer {
    padding: 20px;               
    text-align: center;         
    background-color: #f1f1f1; 
    border-top: 1px solid #ccc;  
    position: relative;          
  }
  
  footer p {
    margin: 0;                  
    font-size: 14px;           
  }
  
  footer a {
    color: rgb(255, 255, 255);                
    text-decoration: underline;   
    transition: color 0.3s;      
  }
  
  footer a:hover {
    color: rgb(246, 255, 0);           
  }
  